<template>
  <div>
    <my-header v-bind:title="title"></my-header>

    <div class="filter-form">
      <el-row>
        <el-col :span="10">
          <el-form ref="filter" label-position="left">
            <el-form-item label=":" label-width="80px">
              <el-col :span="12">
                <el-input v-model="search_superstore_name"></el-input>
              </el-col>
              <el-col class="line" :span="1"></el-col>
              <el-col :span="5">
                <el-button type="primary" @click="summit_search"></el-button>
              </el-col>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span="10">
          <span>: <font class="font-title">{{ superstore_total }}</font> | </span>  
          <span>: <font class="font-title">{{ store_total }}</font> | </span> 
          <span>: <font class="font-title">{{ discount_total }}</font> | </span>
          <span>: <font class="font-title">{{ today_discount }}</font> | </span>
          <span>: <font class="font-title">{{ yesterdat_discount }}</font> | </span>
        </el-col>
      </el-row>
      <el-row>
        <el-col class="filter-tool" :span="10">
          <el-form>
            <el-button type="primary" @click="add_superstore"></el-button>
            <el-button @click="filter_lower"></el-button>
            <el-button @click="filter_online"></el-button>
            <!--  -->
            <el-button type="primary" @click="filter_delete"></el-button>
          </el-form>
        </el-col>
      </el-row>
    </div>

    <el-table
      ref="multipleTable"
      :data="list"
      style="width: 100%;"
      v-loading="table_loading"
      :row-class-name="tableRowClassName"
      @selection-change="handleSelectionChange"
    >
      <el-table-column
        type="selection"
        width="55"
        :default-sort="{prop: 'name', order: 'descending'}"
      ></el-table-column>
      <el-table-column prop="id" label="ID"></el-table-column>

      <el-table-column prop="superstore_name" label=""></el-table-column>

      <el-table-column prop="type_" label="" sortable>
        <template slot-scope="scope">
          <el-tag size="medium">{{ scope.row.type_ }}</el-tag>
        </template>
      </el-table-column>

      <el-table-column prop="store_num" label=""></el-table-column>

      <el-table-column prop="discount_num" label=""></el-table-column>

      <el-table-column prop="remark" label=""></el-table-column>

      <el-table-column fixed="right" label="">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="showDetail(scope.row.id)"></el-button>
          <el-button @click="edit(scope.row.id)" type="text" size="small"></el-button>
          <el-button @click="go_store(scope.row.id)" type="text" size="small"></el-button>
          <!--  -->
          <el-dropdown @command="handTool">
            <el-button type="text" size="small">
              
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item
                command="lower"
                :data-value="scope.row.id"
                v-if="scope.row.status == 1"
              ></el-dropdown-item>
              <el-dropdown-item
                command="onLine"
                :data-value="scope.row.id"
                v-if="scope.row.status == 2"
              ></el-dropdown-item>
              <el-dropdown-item command="delete" :data-value="scope.row.id"></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>

    <div class="pagination">
      <el-pagination
        class="page"
        @current-change="changeCurrentPage"
        @size-change="changeSizePage"
        :current-page="current_page"
        :page-sizes="pageSizes"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>

    <!--  -->
    <div class="message">
      <el-dialog title="" :visible.sync="simpleVisible" width="30%">
        <div v-loading="simple_loading">
          <ul>
            <li>
              <span class="sueper_name">:</span>
              <span class="param" :title="simpleData.superstore_name">{{simpleData.superstore_name}}</span>
            </li>
            <li>
              <span class="sueper_name">:</span>
              <span class="param" :title="simpleData.type_">{{simpleData.type_}}</span>
            </li>
            <li>
              <span class="sueper_name">:</span>
              <span class="param" :title="simpleData.popularity">{{simpleData.popularity}}</span>
            </li>
            <li>
              <span class="sueper_name">:</span>
              <span class="param" :title="simpleData.longitude">{{simpleData.longitude}} , {{simpleData.latitude}}</span>
            </li>
            <li @mouseenter="show_(2,$event)" @mouseover="hidden_(2,$event)">
              <span class="sueper_name">:</span>
              <span class="param" :title="simpleData.do_business">{{simpleData.do_business}}</span>
            </li>
            <li>
              <span class="sueper_name">:</span>
              <span class="param" :title="simpleData.address">{{simpleData.address}}</span>
            </li>
          </ul>
        </div>
      </el-dialog>
    </div>

    <!--  -->
    <el-dialog :title="inputSuperStoreTitle" :visible.sync="inputSuperStore">
      <el-form
        :model="superstore_form"
        ref="superstore_form"
        v-loading="edit_loadding"
        label-width="80px"
        :rules="rules"
      >
        <el-form-item label="" prop="superstore_name">
          <el-input v-model="superstore_form.superstore_name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-col :span="26">
            <el-select v-model="superstore_form.type" placeholder="">
              <el-option
                v-for="(item, index) in types"
                :key="index"
                :value="item.key"
                :label="item.value"
              ></el-option>
            </el-select>
          </el-col>
        </el-form-item>

        <el-form-item label="" prop="type">
          <el-col :span="26">
            <el-select v-model="superstore_form.status" placeholder="">
              <el-option value="" label=""></el-option>
              <el-option value="" label=""></el-option>
            </el-select>
          </el-col>
        </el-form-item>

        <el-form-item label="" prop="popularity">
          <el-input v-model="superstore_form.popularity"></el-input>
        </el-form-item>
        <el-form-item label="" :inline="true" prop="longitude_latitude">
          <el-col :span="8">
            <el-input v-model="superstore_form.longitude"></el-input>
          </el-col>
          <el-col class="line" :span="2">-</el-col>
          <el-col :span="8">
            <el-input v-model="superstore_form.latitude"></el-input>
          </el-col>
          <el-col class="line" :span="2">-</el-col>
          <el-col :span="3">
            <el-button type="primary" @click="onAddress"></el-button>
          </el-col>
        </el-form-item>

        <el-form-item label="" prop="do_business">
          <el-input v-model="superstore_form.do_business"></el-input>
        </el-form-item>
        <el-form-item label="" prop="address">
          <el-input v-model="superstore_form.address"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cacheEdit = false"> </el-button>
        <el-button type="primary" v-if="inputSuperStoreTitle == ''"  @click="confirmUpdate('superstore_form')"></el-button>
        <el-button type="primary" v-if="inputSuperStoreTitle == ''"  @click="confirmCreate('superstore_form')"></el-button>
      </div>
    </el-dialog>

    <el-dialog title="" :visible.sync="amap" class="amap-body">
      <div class="dialog-content amap-content">
        <el-amap vid="amapDemo" :center="center" :zoom="zoom" class="amap-demo" :events="events"></el-amap>
        <span style="color: red;">
          {{superstore_form.longitude}} - {{superstore_form.latitude}}
          <br>
          {{superstore_form.address}}
        </span>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="amap = false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import myHeader from "./common/Header";

export default {
  name: "SuperStore",
  mounted() {
    this.getList()
    this.getDataInfo()
  },
  components: {
    myHeader
  },
  data() {
    var self = this;
    return {
      id: 0,
      title: "",
      inputSuperStoreTitle: '',
      table_loading: true,
      simple_loading: true,
      edit_loadding: true,
      list: [],
      pagesize: 10,
      pageSizes: [5, 10, 15, 20],
      total: 100,
      current_page: 1,
      simpleData: {},
      simpleVisible: false,
      inputSuperStore: false,
      amap: false,
      visible: false,
      superstore_form: {},
      superstore_total: 0,
      store_total: 0,
      discount_total: 0,
      today_discount: 0,
      multipleSelection: '',
      multipleSelectionIds: [],
      yesterdat_discount: 0,
      // 
      search_superstore_name: "",
      //   0 1 2
      types: [
        {
          key: 0,
          value: ""
        },
        {
          key: 1,
          value: ""
        },
        {
          key: 2,
          value: ""
        }
      ],
      rules: {
        superstore_name: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          {
            mix: 2,
            max: 100,
            message: "2-100",
            trigger: "blur"
          }
        ],
        popularity: [
            {
              required: true,
              message: "",
              trigger: "blur"
            }
        ],
        do_business: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        address: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ]
      },
      zoom: 12,
      center: [114.12593, 22.649967],
      events: {
        click(e) {
          let { lng, lat } = e.lnglat;
          self.superstore_form.longitude = lng;
          self.superstore_form.latitude = lat;

          //  SDK 
          var geocoder = new AMap.Geocoder({
            radius: 1000,
            extensions: "all"
          });

          var auto = new AMap.Autocomplete({
            input: "amap_search"
          });

          geocoder.getAddress([lng, lat], function(status, result) {
            if (status === "complete" && result.info === "OK") {
              if (result && result.regeocode) {
                self.superstore_form.address =
                  result.regeocode.formattedAddress;
                  self.$nextTick();
              }
            }
          });
        }
      },
      lng: 0,
      lat: 0
    };
  },
  methods: {
    // 
    edit(id) {
      this.inputSuperStoreTitle = ""
      this.inputSuperStore = true;
      this.getSimple(id);
      this.edit_loadding = false;
    },
    // 
    getList() {
      this.$get("/api/superstore", {
        page: this.current_page,
        pagesize: this.pagesize
      }).then(response => {
        this.list = response.data.list;
        this.setPage(response.data);
        this.table_loading = false;
      });
    },
    // 
    setPage(data) {
      console.log(data);
      this.current_page = data.page;
      this.total = data.total;
      this.pagesize = parseInt(data.pagesize);
    },
    // 
    changeCurrentPage(page) {
      this.current_page = page;
      this.getList();
    },
    changeSizePage(pagesize) {
      this.pagesize = pagesize;
      this.getList();
    },
    // 
    showDetail(id) {
      this.getSimple(id);
      this.simpleVisible = true;
      this.simple_loading = false;
    },
    // 
    getSimple(id) {
      this.id = id;
      this.$get(`/api/superstore/${id}`).then(response => {
        if (response.code == 200) {
          this.simpleData = response.data
          this.superstore_form = response.data
          if(this.superstore_form.status == 1) {
            this.superstore_form.status = ""
          }else{
            this.superstore_form.status = ""
          }
        }
      });
    },
    show_(index, e) {
      console.log(e.target);
    },
    hidden_(index, e) {
      console.log(e);
    },
    // 
    cacheEdit() {},
    // 
    confirmUpdate(form) {
      this.$refs[form].validate(valid => {
        if (valid) {
          this.edit_loadding = true;
          this.updateData(this.superstore_form);
        } else {
          return false;
        }
      });
    },

    updateData(data) {
      var postData = {
        superstore_name: data.superstore_name,
        longitude: data.longitude,
        latitude: data.latitude,
        type: data.type,
        popularity: data.popularity,
        do_business: data.do_business,
        address: data.address,
        status: data.status == '' ? 1 : 2
      };

      this.$put(`/api/superstore/` + this.id, postData).then((response) => {
        if (response.code == 200) {
          this.edit_loadding = false;
          this.$success_("");
          this.getList();
        } else {
          this.$error_(response.msg);
        }
      });
    },

    // 
    onAddress() {
      this.amap = true;
      if(this.superstore_form.longitude) {
        this.center = [
          this.superstore_form.longitude,
          this.superstore_form.latitude
        ];
      }
    },

    // 
    handTool(command, e) {
      var id = e.$attrs["data-value"];
      if (command == "lower") {
        this.lower(id);
      } else if (command == "delete") {
        this.delete(id);
      } else if (command == "onLine") {
        this.onLine(id);
      }
    },

    // 
    lower(id) {
      this.$post(`/api/superstore/lower/${id}`).then((r) => {
        if(r.code == 200) {
          this.$success_("");
          this.getList()
        }else{
          this.$error_(r.msg);
        }
      })
    },

    // 
    onLine(id) {
      this.$post(`/api/superstore/online/${id}`).then((r) => {
        if(r.code == 200) {
          this.$success_("");
          this.getList()
        }else{
          this.$error_(r.msg);
        }
      })
    },

    // 
    delete(id) {
      this.$deletes(`/api/superstore/${id}`).then((r) => {
        if(r.code == 200) {
          this.$success_("");
          this.getList()
        }else{
          this.$error_(r.msg);
        }
      })
      this.$error_("");
    },

    tableRowClassName({ row }) {
      if (row.status == 2) {
        row.remark = "- ";
        return "color-lower";
      }
    },

    // 
    // 
    summit_search() {
      // this.$post('')
      this.$success_('')
    },

    // 
    add_superstore() {
      this.superstore_form = {}
      this.inputSuperStore = true;
      this.inputSuperStoreTitle = ""
      this.edit_loadding = false;

      this.superstore_form.type = 1
      this.superstore_form.status = 1
      this.superstore_form.popularity = 5
    },

    // 
    confirmCreate() {
       this.$refs[form].validate(valid => {
        if (valid) {
          console.log(this.superstore_form);
          this.edit_loadding = true;
          this.post_superstore(this.superstore_form);
        } else {
          return false;
        }
      });
    },

    post_superstore(data) {
       var postData = {
        superstore_name: data.superstore_name,
        longitude: data.longitude,
        latitude: data.latitude,
        type: data.type,
        popularity: data.popularity,
        do_business: data.do_business,
        address: data.address,
        status: data.status
      };

       this.$post('/api/superstore', postData)
          .then(function(response){
            if(response.status == 1) {
              this.$success_('')
              this.getList()
            }
          })
    },

    // 
    filter_online() {
      this.getSelected()
      let ids = this.multipleSelectionIds.join(',')
      this.$post(`/api/superstore/batch_online?ids=${ids}`).then((r) => {
        if(r.code == 200) {
          this.$success_('')
          this.getList()
        }else{
          this.$error_(r.msg)
        }
      })

    },

    // 
    filter_lower() {
      this.getSelected()
      let ids = this.multipleSelectionIds.join(',')
      this.$post(`/api/superstore/batch_lower?ids=${ids}`).then((r) => {
        if(r.code == 200) {
          this.$success_('')
          this.getList()
        }else{
          this.$error_(r.msg)
        }
      })

    },

    // 
    filter_delete() {
      this.$error_('')
    },

    // 
    go_store(id) {
      localStorage.setItem("superstore_id", id);
      this.$router.push({'path': '/admin/store'})
    },

    // 
    getDataInfo() {
      this.$get('/api/gather').then((r) => {
        if(r.code == 200) {
          this.superstore_total = r.data.superstore_total
          this.store_total = r.data.store_total
          this.discount_total = r.data.discount_total
          this.today_discount = r.data.today_discount
          this.yesterdat_discount = r.data.yesterdat_discount
        }
      })
    },

    // 
    handleSelectionChange(val) {
      this.multipleSelection = val
      this.getSelected()
    },

    // 
    getSelected() {
      var data = this.multipleSelection
      var ids = []
      data.forEach((row) => {
          ids.push(row.id)
      })
      this.multipleSelectionIds = ids
    }
   }
};
</script>

<style lang="less">
.pagination {
  margin-top: 20px;
  .page {
    float: right;
    margin-right: 10%;
  }
}

.message {
  ul,
  li {
    padding: 0;
    margin: 0;
    list-style: none;
    color: black;
    font-weight: bold;
  }

  li {
    display: block;
    height: auto;
    overflow: hidden;
    margin-top: 15px;
  }

  .sueper_name,
  .param {
    display: block;
    float: left;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .sueper_name {
    width: 30%;
    text-align: right;
    padding-right: 20px;
    color: #808080;
  }

  .param {
    width: 50%;
    text-align: left;
  }
}

.amap-content {
  height: 500px;
  width: 100%;
}

.el-dropdown-link {
  cursor: pointer;
  color: #409eff;
}
.el-icon-arrow-down {
  font-size: 12px;
}

.color-lower {
  color: #d0d0d0;
}

.filter-form {
  padding-left: 30px;

  .filter-tool {
    .el-button {
      float: left;
    }
  }
}
.font-title{
  color: red
}
</style>



